
<!-- QuickPlan Main -->
<template>
  <div class="guide_main">
    <el-row  class="step_indicator">

      <el-col :span="3">
        <router-link to="/Guide_entrance">
          <el-button type="text" style="margin-left:24px;font-size:18px;">← 回到最初</el-button>
        </router-link>
      </el-col>
      <el-col :span="18">
        <el-steps  :active="active" finish-status="finish">
          <el-step title="" description=""></el-step>
          <el-step title="" description=""></el-step>
          <el-step title="" description=""></el-step>
          <el-step title="" description=""></el-step>
          <el-step title="" description="" icon="el-icon-check"></el-step>
        </el-steps>
      </el-col>
    </el-row>
    <div>
    </div>
    <div class="step_content" >
      <!-- 目前的transition是右滑左 -->
      <transition mode="out-in"
       enter-active-class="animated fadeInRight fast" 
       leave-active-class="animated fadeOutLeft faster ">
        <router-view style=""/>
      </transition>
      
    </div>
    <div class="step_controller">
      <el-button id="guide_btn_prev" @click="prev" round>← 上一步</el-button>
      
      <div style="display:inline-flex">
        <el-button id="guide_btn_contact"  round>咨詢顧問</el-button>
        <el-button id="guide_btn_export"  round>輸出規劃書</el-button>
        <el-button id="guide_btn_goCalculate" @click="menu_on" type="primary" round style="">將結果帶入計算頁面，進行微調</el-button>
      </div>
      <el-button id="guide_btn_next" @click="next" round>下一步 →</el-button>
    </div>
  </div>
</template>

<script>
import animate from 'animate.css'
import router from './router'
  export default {
    data() {
      return {
        active: 0
      };
    },
    methods: {
      next() {
        this.active++;
        if (this.active >= 4){
            this.active = 4;
            document.getElementById("guide_btn_goCalculate").style.display = 'block';
            document.getElementById("guide_btn_contact").style.display = 'block';
            document.getElementById("guide_btn_export").style.display = 'block';
            document.getElementById("guide_btn_next").style.display = 'none';
        }
        router.push( '/'+(this.active+1) );
        // alert('/'+(this.active+1));
      },
      prev() {
        this.active--;
        if (this.active < 1) this.active = 0;
        router.push( '/'+(this.active+1) );
        document.getElementById("guide_btn_goCalculate").style.display = 'none';
        document.getElementById("guide_btn_contact").style.display = 'none';
        document.getElementById("guide_btn_export").style.display = 'none';
        document.getElementById("guide_btn_next").style.display = 'block';
      },
      menu_on(){
        var menu = document.getElementsByClassName("sidebar")[0];
        menu.classList.toggle("hide");
        // menu.display = menu.style.display === 'flex' ? '' : 'none';
      }
    }
  }
</script>

